import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import "../components/layout.css"

const HeaderNav = styled.header`
  display: flex;
  align-items: center;
  justify-content: space-between;
`

const HeaderLogo = styled.h1`
  font-size: 36px;
`

const HeaderUL = styled.ul`
  display: flex;
`

const HeaderLI = styled.li`
  margin: 0 2rem;
  list-style-type: none;
  font-size: 18px;
  letter-spacing: 1px;
`

const Header = () => {
  return (
    <HeaderNav>
      <HeaderLogo>
        <AniLink paintDrip duration={0.4} hex="#212840" to="/">
          MD
        </AniLink>
      </HeaderLogo>
      <nav>
        <HeaderUL>
          <HeaderLI>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/">
              Home
            </AniLink>
          </HeaderLI>
          <HeaderLI>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/about/">
              About
            </AniLink>
          </HeaderLI>
          <HeaderLI>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/skills/">
              Skills
            </AniLink>
          </HeaderLI>
          <HeaderLI>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/projects/">
              Projects
            </AniLink>
          </HeaderLI>
          <HeaderLI>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/contact/">
              Contact
            </AniLink>
          </HeaderLI>
        </HeaderUL>
      </nav>
    </HeaderNav>
  )
}

export default Header
